import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { SearchLayoutComponent } from './layouts/search-layout/search-layout.component';
import { DetailComponent } from './pages/detail/detail.component';
import { SearchDocumentsComponent } from './pages/documentos/search-documents/search-documents.component';
import { FilterTabsComponent } from './components/filter-tabs/filter-tabs.component';
import { SearchResolucionesComponent } from './pages/documentos/search-resoluciones/search-resoluciones.component';
import { SearchEscritosComponent } from './pages/documentos/search-escritos/search-escritos.component';

const routes: Routes = [
  { path: '', pathMatch: 'full', redirectTo: 'documentos' },
  { path: '', component: SearchLayoutComponent, children:
  [
      { path: 'documentos', component: SearchDocumentsComponent },
      { path: 'resoluciones', component: SearchResolucionesComponent },
      { path: 'escritos', component: SearchEscritosComponent },
    ]
  },

  { path: 'detail', component: DetailComponent },
  { path: '**', redirectTo: "documentos" }
];

@NgModule({
  imports: [
    RouterModule.forRoot(routes)
  ],
  exports: [RouterModule],
  declarations: [
    SearchLayoutComponent,
    DetailComponent,
    SearchDocumentsComponent,
    SearchEscritosComponent,
    SearchResolucionesComponent,
    FilterTabsComponent,
  ]
})
export class AppRoutingModule { }
